.info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 15px 0;
  min-height: 60px;

  &__rights {

    &-description {
      font-size: $prs;
      line-height: $prs*1.1;
      margin-bottom: 10px;

      color: $easygrey;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__company {
    font-family: $bold;
    font-size: $prs*1.4;
    line-height: $prs*2.4;
    letter-spacing: 0.05em;

    color: $company-grey;
  }
}

@media (max-width: $desktop-width) {
  .info {
    &__company {
      margin-left: auto;
    }
    
    &__rights {
      width: 100%;
      margin-bottom: 25px;
    }
  }
}
